@import "../../global.scss";

.Testimonial {
  background-color: lightgreen;
  display: flex;
  align-items: center;
  flex-direction: column;

  @include mobile{
      justify-content: space-around;
  }

  h1 {
    font-size: 50px;
    @include mobile {
      font-size: 30px;
    }
  }

  .container {
    width: 100%;
    height: 85%;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
      flex-direction: column;
      height: 100%;
    }
    .card {
      width: 250px;
      height: 70%;
      border-radius: 10px;
      box-shadow: 0px 0px 15px -8px black;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 20px;
      transition: all 1s ease;

      @include mobile {
        height: 200px;
        margin: 10px 0;
      }

      &.featured {
        width: 300px;
        height: 75%;
        margin: 0 30px;
        @include mobile {
          width: 250px;
          height: 200px;
          margin: 10px;
        }
      }
      &:hover {
        transform: scale(1.1);
      }
      .top {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          &.left,
          &.right {
            height: 25px;
          }
          &.user {
            height: 180px;
            width: 180px;
            border-radius: 10%;
            object-fit: cover;
            margin: 0 30px;
            @include mobile {
              width: 75px;
              height: 75px;
            }
          }
        }
      }
      .center {
        padding: 10px;
        border-radius: 10px;
        background-color: rgb(207, 236, 227);
        box-shadow: 0px 0px 15px -8px black;
        @include mobile {
          font-size: 12px;
          padding: 5px;
        }
      }
      .bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        h3 {
          margin-bottom: 5px;
          @include mobile {
            font-size: 15px;
          }
        }
        h4 {
          color: gray;
          @include mobile {
            font-size: 10px;
          }
        }
      }
    }
  }
}
